.backdrop {
  z-index: 1000;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  transition: opacity var(--animation-timing-function), visibility var(--animation-timing-function);
  background-color: rgba(55, 57, 63, .204);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

